import React, {useState, useEffect} from "react"
import { HashLink as Link } from 'react-router-hash-link';
import {Button} from './Button'
import './Navbar.css'

const Navbar = () => {

    const [click, setClick] = useState(false)
    const [button, setButton] = useState(true)

    const handleClick = () => setClick(!click)
    const closeMobileMenu = () => setClick(false)
    
    const showButton = () => {
        if(window.innerWidth <= 960) {
            setButton(false)
        } else {
            setButton(true)
        }
    }

    useEffect(() => {
        showButton();
        window.addEventListener('resize', showButton) // move this to useEffect for clean up
        return window.removeEventListener('resize', showButton)
      }, []);

    // maybe use css resize for smoother actions

    return (
        <>
            <nav className="navbar">
                <div className="navbar-container">
                    <Link to="/" className="navbar-logo" onClick={closeMobileMenu}> <i class="fas fa-landmark"></i></Link>
                    <div className="menu-icon" onClick={handleClick}>
                        <i className={click ? "fas fa-times" : "fas fa-bars"} />
                    </div>
                    <ul className={click? "nav-menu active" : "nav-menu" }>
                        <li className="nav-item">
                            <Link to="/" className="nav-links" onClick={closeMobileMenu}>Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="#Projects" className="nav-links" onClick={closeMobileMenu}>Projects</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="#Bio" className="nav-links" onClick={closeMobileMenu}>Bio</Link>
                        </li>
                    </ul>
                    {/* {button && <Button buttonStyle='btn--outline' destination='/projects'>Projects!</Button>} */}
                </div>
            </nav>
        </>
    )
}

export default Navbar
