import React from 'react'
import { Button } from './Button'
import './Hero.css'
import '../App.css' // nope

const Hero = () => {
    return (
        <div className='hero-container'>
            <h1>Code <strikethrough>is</strikethrough> can be Art</h1>
            <p>...and many other non-memorable quotes</p>
            <div className="hero-btns">
                <Button className='btns' buttonStyle='btn--outline' buttonSize='btn--large' destination='/#Projects'>
                  <i class="fas fa-bug"></i> View Projects
                </Button>
                <Button className='btns' buttonStyle='btn--primary' buttonSize='btn--large' destination='/#Projects'>
                 <i class="fas fa-concierge-bell"></i> Contact Info 
                </Button>
            </div>
        </div>
    )
}

export default Hero
