import React from 'react'
import './Cards.css';
import CardItem from './CardItem';

import billy from '../static/images/building-768815_1280.jpg';
import mathias from '../static/images/computer-1209641_1280.jpg';
import clayton from '../static/images/server-1235959_1280.jpg';
import ferdi from '../static/images/stock-1863880_1280.jpg';
import raghav from '../static/images/raghav-yadav-l7M7_tuqrZI-unsplash.jpg';



const Cards = () => {
    return (
        <div className='cards'>
        <div className='cards--container'>
          <div className='cards--wrapper'>
            <ul className='cards--items'>
              <CardItem
                src={ferdi}
                text='Projects Explore the hidden waterfall deep inside the Amazon Jungle'
                label='Data Science'
                path='/Projects'
              />
              <CardItem
                src={mathias}
                text='Bio Travel through the Islands of Bali in a Private Cruise'
                label='Programming'
                path='/Bio'
              />
            </ul>
            <ul className='cards--items'>
              <CardItem
                src={clayton}
                text='Projects Set Sail in the Atlantic Ocean visiting Uncharted Waters'
                label='Back-end'
                path='/Projects'
              />
              <CardItem
                src={billy}
                text='Bio Experience Football on Top of the Himilayan Mountains'
                label='Design'
                path='/Bio'
              />
              <CardItem
                src={raghav}
                text='Bio Experience Football on Top of the Himilayan Mountains'
                label='Photography'
                path='/Bio'
              />
            </ul>
          </div>
        </div>
      </div>
    )
}

export default Cards
